body {
  text-align: center;
}

.inputArea {
  display: inline-block;
  margin-left: 20px;
  margin-right: 20px;
  vertical-align: top;
}

.label {
  display: inline-block;
  margin-bottom: 5px;
  margin-right: 5px;
}

.info {
  border-radius: 100px;
  padding: 2px 6px;
  background-color: #ddd;
  font-size: 12px;
  font-family: monospace;
  cursor: default;
}

.refPoint {
  width: 0;
  height: 0;
  position: relative;
  display: inline-block;
}

.infoBox {
  position: absolute;
  background-color: #e9e9e9;
  border-radius: 3px;
  -webkit-box-shadow: 0 2px 5px 0 #ddd;
          box-shadow: 0 2px 5px 0 #ddd;
  padding: 10px;
  width: 100px;
  left: 5px;
  font-size: 14px;
  bottom: -20px;
}

.input {
  display: block;
  border-radius: 3px;
  padding: 5px;
  border: 1px solid #ddd;
  -webkit-box-shadow: inset 0 2px 5px 0 #ddd;
          box-shadow: inset 0 2px 5px 0 #ddd;
  font-size: 20px;
  margin: auto;
  margin-bottom: 20px;
}

.error {
  border: 1px solid red !important;
  -webkit-box-shadow: inset 0 0 5px 0 red !important;
          box-shadow: inset 0 0 5px 0 red !important;
}

.inputErrorText {
  display: none;
  color: red;
  margin-top: -10px;
}

.submit {
  border: none;
  background-color: #02a0f1;
  border-bottom: 2px solid #00466b;
  color: white;
  padding: 10px;
  border-radius: 2px;
  font-size: 16px;
  cursor: pointer;
  display: block;
  margin: auto;
}

.submit:focus {
  outline: none;
  background-color: #00466b;
  border-bottom: 2px solid #00466b;
}


